import React from 'react';
import { useHistory } from 'react-router';

import AppointmentLayout from './Layout';

import { useAppointment } from 'store/appointment';
import { APPOINTMENT_STEPS } from 'configs/constants';

import ic_success from 'assets/images/book_appointment_success.gif';

const Finish = () => {
  const history = useHistory();
  const { setStep } = useAppointment();

  /**
   * Handle confirm
   */
  const handleConfirm = () => {
    setStep(APPOINTMENT_STEPS.TOPICS);
    history.push('/consulting');
  };

  return (
    <AppointmentLayout title="Vielen Dank" btnBack="" btnClose="" btnConfirm="Abschließen" onConfirm={handleConfirm}>
      <div className="Finish-area">
        <div className="Finish-container">
          <p className="line1">
            Wir prüfen nun Ihre Anfrage und suchen den für Ihr Anliegen am besten geeigneten Pflege-Experten aus.
          </p>
          <p className="line2">
            Wir bestätigen Ihren Termin in Kürze per E-Mail. Zum Zeitpunkt der
            <br />
            Videosprechstunde treten Sie dem Gespräch über Ihre App bei.
          </p>
          <img src={ic_success} alt="" />
        </div>
      </div>
    </AppointmentLayout>
  );
};

export default Finish;
