import React, { useEffect } from 'react';
import { RouteComponentProps } from 'react-router-dom';

import Topics from './Topics';
import EmergencyAid from './EmergencyAid';
import AdviceForWhom from './AdviceForWhom';
import Survey from './Survey';
import Schedule from './Schedule';
import AlmostDone from './AlmostDone';
import Cost from './Cost';
import Overview from './Overview';
import Finish from './Finish';

import { APPOINTMENT_STEPS } from 'configs/constants';
import { useAppointment } from 'store/appointment';

import './Layout.scss';

const { EMERGENCY_AID, ADVICE_FOR_WHOM, SURVEY, SCHEDULE, ALMOST_DONE, COST, OVERVIEW, FINISH } = APPOINTMENT_STEPS;

const AppointmentMainPage: React.FC<RouteComponentProps> = () => {
  const { step, resetAppointmentData } = useAppointment();

  useEffect(() => {
    return () => {
      resetAppointmentData();
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  /**
   * Handle render children form
   */
  const renderChildren = () => {
    switch (step) {
      case EMERGENCY_AID:
        return <EmergencyAid />;
      case ADVICE_FOR_WHOM:
        return <AdviceForWhom />;
      case SURVEY:
        return <Survey />;
      case SCHEDULE:
        return <Schedule />;
      case ALMOST_DONE:
        return <AlmostDone />;
      case COST:
        return <Cost />;
      case OVERVIEW:
        return <Overview />;
      case FINISH:
        return <Finish />;
      default:
        return <Topics />;
    }
  };

  return <>{renderChildren()}</>;
};

export default AppointmentMainPage;
