import React from 'react';

import { SetSchedule } from 'components/screens/user';
import AppointmentLayout from './Layout';

import { useAppointment } from 'store/appointment';
import { APPOINTMENT_STEPS } from 'configs/constants';
import { ScheduleData } from 'types/appointments';

const Schedule = () => {
  const { appointmentData, setStep, updateAppointmentData } = useAppointment();
  const { scheduleData = {} } = appointmentData;

  /**
   * Handle back
   */
  const handleBack = () => {
    setStep(APPOINTMENT_STEPS.SURVEY);
  };

  /**
   * Handle confirm
   *
   * @param data Schedule data
   */
  const handleConfirm = (data: ScheduleData) => {
    setStep(APPOINTMENT_STEPS.ALMOST_DONE);
    updateAppointmentData({ scheduleData: data });
  };

  return (
    <AppointmentLayout title="In welchen Zeitraum sind Sie verfügbar?" onBack={handleBack}>
      <SetSchedule scheduleData={scheduleData} onConfirm={handleConfirm} />
    </AppointmentLayout>
  );
};

export default Schedule;
