import { useMemo, useState } from 'react';
import { useTranslation } from 'react-i18next';
import moment from 'moment';

import { ButtonOptions, FormGroupInput } from 'components';
import { HBModalConfirm } from 'hbBaseClass';
import AppointmentLayout from './Layout';

import { useAppointment } from 'store/appointment';
import { APPOINTMENT_STEPS, GENDER_OPTIONS, SCHEDULE_OPTIONS } from 'configs/constants';
import { SUCCESS, useApi } from 'store/callApi';
import config from 'config';
import { getBookAppointmentRequestParams, isValidBookAppointmentSchedule } from 'utilities';

const { COST, FINISH, SCHEDULE, ALMOST_DONE } = APPOINTMENT_STEPS;

/**
 * Format schedule date
 *
 * @param selectedOption Select date option
 * @param specificDate Specific date when user select option LATER
 * @param formatDate Format date
 *
 * @return string
 */
const formatScheduleDate = (selectedOption: string, specificDate: Date, formatDate = 'DD.MM.YYYY') => {
  let scheduleDate = moment();
  if (selectedOption !== SCHEDULE_OPTIONS.TODAY) {
    scheduleDate = selectedOption === SCHEDULE_OPTIONS.LATER ? moment(specificDate) : moment().add(1, 'd');
  }

  return scheduleDate.format(formatDate);
};

const Overview = () => {
  const { t } = useTranslation();
  const { callApi } = useApi();
  const { appointmentData, topics, setStep } = useAppointment();

  // states
  const [isOpenPopup, setIsOpenPopup] = useState(false);

  const { selectedTopic, scheduleData = {}, userInfo = {} } = appointmentData;
  const { selectedOption, specificDate, timeStart, timeEnd } = scheduleData;
  const { phone_number = '', gender } = userInfo;

  // Get topic name
  const topicName = useMemo(() => {
    const topic = topics.find((topicItem: any) => topicItem.id === selectedTopic) || {};

    return topic.tile || '';
  }, [topics, selectedTopic]);

  // Get schedule date
  const { scheduleDate, scheduleDateYMD } = useMemo(
    () => ({
      scheduleDate: formatScheduleDate(selectedOption, specificDate),
      scheduleDateYMD: formatScheduleDate(selectedOption, specificDate, 'YYYY-MM-DD'),
    }),
    [selectedOption, specificDate],
  );

  /**
   * Handle request book an appointment data
   */
  const sendDataBookAnAppointment = () => {
    // get book an appointment request params
    const requestParams = getBookAppointmentRequestParams(appointmentData);

    callApi(
      {
        method: 'post',
        api: config.rest.createAppointment(),
        body: requestParams,
        loading: true,
        msg: t('errorUnknown'),
      },
      (response: any) => {
        const { status } = response;
        if (status !== SUCCESS) return;

        setStep(FINISH);
      },
    );
  };

  /**
   * Handle submit
   */
  const handleSubmit = () => {
    // Check valid schedule
    const isValidSchedule = isValidBookAppointmentSchedule({ selectedOption, specificDate, timeStart });
    // check match schedule date
    const currentDate = moment().format('YYYY-MM-DD');

    if (!isValidSchedule || currentDate > scheduleDateYMD) {
      setIsOpenPopup(true);
      return;
    }

    sendDataBookAnAppointment();
  };

  return (
    <AppointmentLayout
      title="Übersicht Ihrer Anfrage"
      btnConfirm="Weiter"
      onBack={() => setStep(COST)}
      onConfirm={handleSubmit}
    >
      <div className="Overview-area">
        <div className="Overview-container">
          <div className="Overview-desc">
            <h5>Ihre Beratung</h5>
            <p>{topicName}</p>
          </div>
          <div className="AlmostDone-gender">
            <h5 className="AppointmentLayout-subLabel">Geschlecht</h5>
            <ButtonOptions className="Overview-options" disabled value={gender} options={GENDER_OPTIONS} />
          </div>
          <div className="Overview-form">
            <FormGroupInput
              label="Datum"
              value={scheduleDate}
              disabled
              btnLabel="ändern"
              btnLabelOnClick={() => setStep(SCHEDULE)}
            />
            <FormGroupInput
              label="Uhrzeit"
              value={`${timeStart || '?'} Uhr – ${timeEnd || '?'} Uhr`}
              disabled
              btnLabel="ändern"
              btnLabelOnClick={() => setStep(SCHEDULE)}
            />
            <FormGroupInput
              label="Ihre Telefonnummer"
              value={phone_number}
              disabled
              btnLabel="ändern"
              btnLabelOnClick={() => setStep(ALMOST_DONE)}
            />
            <FormGroupInput label="Zahlungsinformationen" value="" disabled btnLabel="ändern" />
          </div>
        </div>
      </div>
      <HBModalConfirm
        show={isOpenPopup}
        handleClose={() => setIsOpenPopup(false)}
        title="Information"
        content="Wir haben nicht genug Zeit, um Ihre Anfrage anzunehmen. Bitte wählen Sie die Zeit erneut"
        up="Verstanden"
        upCallback={() => setStep(SCHEDULE)}
      />
    </AppointmentLayout>
  );
};

export default Overview;
