import React, { useMemo, useState } from 'react';
import { useHistory } from 'react-router-dom';

import { HBButtonSmall, HBModalConfirm } from 'hbBaseClass';
import { ProgressLine } from 'components';
import { AppointmentBtnConfirm } from 'components/screens';

import { APPOINTMENT_STEPS } from 'configs/constants';
import { useAppointment } from 'store/appointment';

import './Layout.scss';

interface PropTypes {
  title?: string;
  btnBack?: string;
  onBack?: (param: any) => void;
  btnClose?: string;
  onClose?: () => void;
  btnConfirm?: string;
  btnConfirmDisabled?: boolean;
  onConfirm?: () => void;
  progressStep?: number;
  children?: React.ReactNode;
}

const { SURVEY, FINISH } = APPOINTMENT_STEPS;
let previousProgressStep = 0;

const AppointmentLayout = (props: PropTypes) => {
  const history = useHistory();
  const { step, questions = [] } = useAppointment();
  // states
  const [isOpenPopup, setIsOpenPopup] = useState(false);
  // props
  const {
    title = '',
    btnBack = 'Zurück',
    btnClose = 'Schließen',
    btnConfirm = '',
    onBack = () => {},
    onConfirm = () => {},
    onClose,
    btnConfirmDisabled,
    progressStep,
    children,
  } = props;

  // Get visual parts data
  const visualParts = useMemo(() => {
    const surveySteps = questions.length - 1; // because step survey = SURVEY + question index (start = 0)
    const totalSteps = FINISH + surveySteps;

    // get current step & previous step
    const currentStep = progressStep || (step > SURVEY ? step + surveySteps : step);
    const previousPercentage = `${(previousProgressStep * 100) / totalSteps}%`;
    previousProgressStep = currentStep;

    return [
      {
        id: '1',
        previous: previousPercentage,
        percentage: `${(currentStep * 100) / totalSteps}%`,
        color: 'var(--aquamarin-90)',
      },
    ];
  }, [progressStep, step, questions]);

  /**
   * Handle cancel book an appointment
   */
  const handleCancelBookAppoinment = () => {
    history.goBack();
  };

  return (
    <div className="hb-mx-20 AppointmentLayout-area">
      <div className="AppointmentLayout-header">
        {btnBack && (
          <HBButtonSmall color="petrol" onClick={onBack}>
            <i className="hb-icon-arrow-left" /> {btnBack}
          </HBButtonSmall>
        )}
        {btnClose && (
          <HBButtonSmall color="petrol" onClick={() => (onClose ? onClose() : setIsOpenPopup(true))}>
            {btnClose} <i className="hb-icon-cross" />
          </HBButtonSmall>
        )}
      </div>
      <div className="AppointmentLayout-wrapperContainer">
        <div className="AppointmentLayout-container">
          {step > 1 && <ProgressLine backgroundColor="var(--petrol-10)" visualParts={visualParts} />}
          {title && <h3 className="AppointmentLayout-title">{title}</h3>}
          {children}
        </div>
      </div>
      {btnConfirm && <AppointmentBtnConfirm title={btnConfirm} onClick={onConfirm} disabled={btnConfirmDisabled} />}
      <HBModalConfirm
        show={isOpenPopup}
        handleClose={() => setIsOpenPopup(false)}
        title="Anfrage abbrechen"
        content={`Sind Sie sicher, dass Sie Ihre Anfrage abbrechen möchten? Ihre bisherigen Eingaben gehen dadurch verloren.\n
          Falls Sie Ihre Eingaben nur überprüfen mööchten, klicken Sie bitte links oben auf den Pfeil zurück.`}
        up="Ja, Anfrage abbrechen"
        upCallback={handleCancelBookAppoinment}
        down="Nein, Anfrage fortsetzen"
        downCallback={() => setIsOpenPopup(false)}
      />
    </div>
  );
};

export default AppointmentLayout;
