import React, { useMemo, useState } from 'react';
import { isEmpty } from 'lodash';

import AppointmentLayout from './Layout';
import { QuestionItem } from 'components/screens';

import { useAppointment } from 'store/appointment';
import { validateBookAppointmentAnswers } from 'utilities';
import { APPOINTMENT_STEPS } from 'configs/constants';

const Survey = () => {
  const { step, questions, appointmentData, setStep, updateAppointmentData } = useAppointment();
  const { surveyAnswers = {} } = appointmentData;

  // states
  const [questionIndex, setQuestionIndex] = useState(
    !isEmpty(surveyAnswers) && questions.length ? questions.length - 1 : 0,
  );
  const [answers, setAnswers] = useState(surveyAnswers);

  // Get question data
  const questionData = useMemo(() => questions[questionIndex]?.questions || [], [questions, questionIndex]);

  // Validate answers
  const isValidAnswers = useMemo(() => validateBookAppointmentAnswers(questionData, answers), [questionData, answers]);

  /**
   * Handle back
   */
  const handleBack = () => {
    if (!questionIndex) {
      setStep(APPOINTMENT_STEPS.ADVICE_FOR_WHOM);
      return;
    }

    setQuestionIndex(questionIndex - 1);
  };

  /**
   * Handle click button confirm
   */
  const handleConfirm = () => {
    if (questionIndex >= questions.length - 1) {
      setStep(APPOINTMENT_STEPS.SCHEDULE);
      updateAppointmentData({ surveyAnswers: answers });
      return;
    }

    setQuestionIndex(questionIndex + 1);
  };

  return (
    <AppointmentLayout
      btnConfirm="Weiter"
      onBack={handleBack}
      onConfirm={handleConfirm}
      btnConfirmDisabled={!isValidAnswers}
      progressStep={step + questionIndex}
    >
      <div className="Survey-area">
        {questionData.map((questionItem: any, index: number) => (
          <QuestionItem key={index} questionData={questionItem} answers={answers} setAnswers={setAnswers} />
        ))}
      </div>
    </AppointmentLayout>
  );
};

export default Survey;
