import React, { useMemo, useState } from 'react';

import { HBButton } from 'hbBaseClass';
import AppointmentLayout from './Layout';
import AddRelativesForm from './AddRelativesForm';
import ListAdviceRelatives from './ListAdviceRelatives';

import { useAppointment } from 'store/appointment';
import { APPOINTMENT_STEPS } from 'configs/constants';

export const MODE_OPTIONS = { MAIN: 1, ADD_NEW: 2, LIST: 3 };
const { MAIN, ADD_NEW, LIST } = MODE_OPTIONS;
const defaultFormData = { first_name: '', last_name: '', email: '' };

const AdviceForWhom = () => {
  const { appointmentData, setStep, updateAppointmentData } = useAppointment();
  const { adviceRelatives = [] } = appointmentData;

  // states
  const [mode, setMode] = useState(adviceRelatives?.length ? LIST : MAIN);
  const [selectedRelative, setSelectedRelative] = useState<number | undefined>(undefined);

  // Get relative form data
  const relativeFormData = useMemo(() => {
    return adviceRelatives[selectedRelative!] || defaultFormData;
  }, [adviceRelatives, selectedRelative]);

  /**
   * Handle click relative item index
   *
   * @param relativeIndex Relative index
   */
  const handleClickRelativeItem = (relativeIndex: number | undefined) => {
    setSelectedRelative(relativeIndex);
    setMode(ADD_NEW);
  };

  /**
   * Handle back to emergency aid screen
   */
  const handleBackToEmergencyAid = () => {
    setStep(APPOINTMENT_STEPS.EMERGENCY_AID);
    updateAppointmentData({ adviceRelatives: [] });
  };

  switch (mode) {
    case ADD_NEW: {
      return <AddRelativesForm selectedRelative={selectedRelative} formData={relativeFormData} setMode={setMode} />;
    }
    case LIST: {
      return <ListAdviceRelatives onClickRelativeItem={handleClickRelativeItem} setMode={setMode} />;
    }
    default:
      return (
        <AppointmentLayout title="Für wem ist die Beratung?" onBack={handleBackToEmergencyAid}>
          <div className="AdviceForWhom-area">
            <div className="AdviceForWhom-actions">
              <HBButton onClick={() => setStep(APPOINTMENT_STEPS.SURVEY)}>Für mich</HBButton>
              <HBButton onClick={() => setMode(ADD_NEW)}>+ Angehörige hinzufügen</HBButton>
            </div>
          </div>
        </AppointmentLayout>
      );
  }
};

export default AdviceForWhom;
