import React, { useMemo, useRef } from 'react';
import { useSelector } from 'react-redux';
import { Formik, Form, Field, FormikValues, FormikProps } from 'formik';
import * as Yup from 'yup';
import { isEmpty } from 'lodash';

import { FormGroupInput, ButtonOptions } from 'components';
import { AppointmentBtnConfirm } from 'components/screens';
import AppointmentLayout from './Layout';
import { useAppointment } from 'store/appointment';
import {
  APPOINTMENT_STEPS,
  MAX_LENGTH_PHONE,
  MIN_LENGTH_EMAIL,
  MAX_LENGTH_EMAIL,
  MAX_LENGTH_STREET,
  MAX_LENGTH_NO,
  MAX_LENGTH_POSTAL_CODE,
  MAX_LENGTH_PLACE,
  GENDER_OPTIONS,
} from 'configs/constants';
import config from 'config';

// Formik validate schema
const validationSchema = () => {
  return Yup.object().shape({
    gender: Yup.string().trim().required(),
    email: Yup.string()
      .trim()
      .required()
      .min(MIN_LENGTH_EMAIL)
      .max(MAX_LENGTH_EMAIL)
      .matches(config.validate.validateEmail(), 'email-format-is-incorrect'),
    phone_number: Yup.string().trim().required().max(MAX_LENGTH_PHONE),
    street: Yup.string().trim().required().max(MAX_LENGTH_STREET),
    no: Yup.string().trim().required().max(MAX_LENGTH_NO),
    postal_code: Yup.string().trim().required().max(MAX_LENGTH_POSTAL_CODE),
    place: Yup.string().trim().required().max(MAX_LENGTH_PLACE),
  });
};

const AlmostDone = () => {
  const { appointmentData, setStep, updateAppointmentData } = useAppointment();
  // reducers
  const auth = useSelector((state: any) => state.auth);
  // ref
  const formRef = useRef<FormikProps<any>>(null);

  const { userInfo = {} } = appointmentData;

  // Get initial values
  const initialValues = useMemo(() => {
    const { gender, mobile = '', email = '', place = '', postcode = '', no = '', street = '' } = auth;

    return !isEmpty(userInfo)
      ? userInfo
      : {
          gender,
          phone_number: mobile,
          email,
          place,
          postal_code: postcode,
          no,
          street,
        };
  }, [userInfo, auth]);

  /**
   * Handle change input
   *
   * @param e React ChangeEvent<HTMLInputElement>
   */
  const handleChangeInput = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;

    switch (name) {
      case 'phone_number':
        if (value && !config.validate.valueTypingExpressionsPhone(e)) return;
        break;
      case 'street':
      case 'place':
        if (!config.validate.valueTypingExpressionsName(e)) return;
        break;
      case 'no':
      case 'postal_code':
        if (!config.validate.valueTypingExpressionsNumber(e)) return;
        break;
    }

    formRef?.current?.setFieldValue(name, value);
  };

  /**
   * Handle back previous step
   */
  const handleBack = () => {
    setStep(APPOINTMENT_STEPS.SCHEDULE);
  };

  /**
   * Handle submit form
   *
   * @param values Formik values
   */
  const handleSubmit = (values: FormikValues) => {
    updateAppointmentData({ userInfo: values });
    setStep(APPOINTMENT_STEPS.COST);
  };

  return (
    <Formik
      initialValues={initialValues}
      validationSchema={validationSchema}
      validateOnMount
      onSubmit={handleSubmit}
      innerRef={formRef}
    >
      {({ values, isValid, setFieldValue }) => (
        <AppointmentLayout title="Gleich geschafft!" onBack={handleBack}>
          <div className="AlmostDone-area">
            <p className="AlmostDone-desc">
              Nur noch wenige Angaben - schon kann ein Pflege-Experte Ihre Anfrage übernehmen.
            </p>
            <div className="AlmostDone-gender">
              <h5 className="AppointmentLayout-subLabel">Geschlecht</h5>
              <ButtonOptions
                value={values.gender}
                options={GENDER_OPTIONS}
                onClick={(value: number) => setFieldValue('gender', value)}
              />
            </div>
            <Form className="AlmostDone-form">
              <Field
                as={FormGroupInput}
                required
                name="phone_number"
                label="Mobilfunknummer"
                maxLength={MAX_LENGTH_PHONE}
                placeholder="Mobilfunknummer"
                onChange={handleChangeInput}
              />
              <Field
                as={FormGroupInput}
                required
                name="email"
                label="E-Mail Adresse"
                maxLength={MAX_LENGTH_EMAIL}
                placeholder="E-Mail Adresse"
                disabled
              />
              <Field
                as={FormGroupInput}
                className="tablet"
                required
                name="street"
                label="Straße"
                maxLength={MAX_LENGTH_STREET}
                placeholder="Straße"
                onChange={handleChangeInput}
              />
              <Field
                as={FormGroupInput}
                className="tablet"
                required
                name="no"
                label="Nr."
                maxLength={MAX_LENGTH_NO}
                placeholder="Nr."
                onChange={handleChangeInput}
              />
              <Field
                as={FormGroupInput}
                className="tablet"
                required
                name="postal_code"
                label="PLZ"
                maxLength={MAX_LENGTH_POSTAL_CODE}
                placeholder="PLZ"
                onChange={handleChangeInput}
              />
              <Field
                as={FormGroupInput}
                className="tablet"
                required
                name="place"
                label="Ort"
                maxLength={MAX_LENGTH_PLACE}
                placeholder="Ort"
                onChange={handleChangeInput}
              />
              <AppointmentBtnConfirm disabled={!isValid} type="submit" title="Weiter" />
            </Form>
          </div>
        </AppointmentLayout>
      )}
    </Formik>
  );
};

export default AlmostDone;
