import React, { useMemo, useRef, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { Formik, Form, Field, FormikValues, FormikProps } from 'formik';
import * as Yup from 'yup';
import { isNil } from 'lodash';

import config from 'config';

import { FormGroupInput } from 'components';
import { AppointmentBtnConfirm } from 'components/screens';
import { useAppointment } from 'store/appointment';
import AppointmentLayout from './Layout';
import { MODE_OPTIONS } from './AdviceForWhom';
import { HBModalConfirm } from 'hbBaseClass';

import { MAX_LENGTH_FIRST_NAME, MAX_LENGTH_LAST_NAME, MIN_LENGTH_EMAIL, MAX_LENGTH_EMAIL } from 'configs/constants';

type PropTypes = {
  selectedRelative?: number;
  formData: {
    first_name: string;
    last_name: string;
    email: string;
  };
  setMode: (value: number) => void;
};

const AddRelativesForm = (props: PropTypes) => {
  const { t } = useTranslation();
  const { appointmentData, updateAppointmentData } = useAppointment();
  // states
  const [isOpenPopup, setIsOpenPopup] = useState(false);
  // props
  const { selectedRelative, formData, setMode } = props;
  // ref
  const formRef = useRef<FormikProps<any>>(null);

  const { adviceRelatives = [] } = appointmentData;

  // Formik validate schema
  const validationSchema = useMemo(() => {
    // get list relative emails use for check duplicate email
    const relativeEmails = adviceRelatives.reduce(
      (acc: any, relativeItem: any, index: number) => (selectedRelative !== index ? [...acc, relativeItem.email] : acc),
      [],
    );

    return Yup.object().shape({
      first_name: Yup.string()
        .trim()
        .required('First Name is required')
        .max(MAX_LENGTH_FIRST_NAME, 'first-name-must-be-at-least-1'),
      last_name: Yup.string()
        .trim()
        .required('Last Name is required')
        .max(MAX_LENGTH_LAST_NAME, 'last-name-must-be-at-least-1-c'),
      email: Yup.string()
        .required('Email is required')
        .min(MIN_LENGTH_EMAIL, 'email-must-be-at-least-3-chara')
        .max(MAX_LENGTH_EMAIL, 'email-must-be-at-least-3-chara')
        .matches(config.validate.validateEmail(), 'email-format-is-incorrect')
        .notOneOf(relativeEmails, 'this-email-already-exists-ple'),
    });
  }, [selectedRelative, adviceRelatives]);

  /**
   * Handle change name
   *
   * @param e React ChangeEvent<HTMLInputElement>
   */
  const handleChangeName = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (!config.validate.valueTypingExpressionsName(e)) return;

    const { name, value } = e.target;
    formRef?.current?.setFieldValue(name, value);
  };

  /**
   * Handle submit form
   *
   * @param values Formik values
   */
  const handleSubmit = (values: FormikValues) => {
    let newAdviceRelatives = [...adviceRelatives];

    if (!isNil(selectedRelative)) {
      newAdviceRelatives[selectedRelative] = values;
    } else {
      newAdviceRelatives = [...newAdviceRelatives, values];
    }

    updateAppointmentData({ adviceRelatives: newAdviceRelatives });
    setMode(MODE_OPTIONS.LIST);
  };

  /**
   * Handle click button go back
   *
   * @param isFormDirty Form dirty status
   */
  const handleGoBack = (isFormDirty: boolean) => {
    if (isFormDirty) {
      setIsOpenPopup(true);
      return;
    }

    handleConfirmDialog();
  };

  /**
   * Handle confirm dialog
   */
  const handleConfirmDialog = () => {
    setMode(adviceRelatives?.length ? MODE_OPTIONS.LIST : MODE_OPTIONS.MAIN);
  };

  return (
    <Formik initialValues={formData} validationSchema={validationSchema} onSubmit={handleSubmit} innerRef={formRef}>
      {({ isValid, dirty, touched, errors }) => (
        <AppointmentLayout title="Angehörige hinzufügen" onBack={() => handleGoBack(dirty)}>
          <div className="AdviceForWhom-area">
            <div className="AdviceForWhom-container">
              <Form className="AddRelativesForm-form">
                <Field
                  as={FormGroupInput}
                  required
                  name="first_name"
                  label="Vorname"
                  maxLength={MAX_LENGTH_FIRST_NAME}
                  placeholder="Vorname"
                  onChange={handleChangeName}
                  msgError={touched.first_name && t(errors.first_name as string)}
                />
                <Field
                  as={FormGroupInput}
                  required
                  name="last_name"
                  label="Nachname"
                  maxLength={MAX_LENGTH_LAST_NAME}
                  placeholder="Nachname"
                  onChange={handleChangeName}
                  msgError={touched.last_name && t(errors.last_name as string)}
                />
                <Field
                  type="email"
                  as={FormGroupInput}
                  required
                  name="email"
                  label="E-Mail Adresse"
                  maxLength={MAX_LENGTH_LAST_NAME}
                  placeholder="E-Mail Adresse"
                  msgError={touched.email && t(errors.email as string)}
                />
                <AppointmentBtnConfirm disabled={!dirty || !isValid} type="submit" title="Speichern" />
              </Form>
            </div>
          </div>
          <HBModalConfirm
            show={isOpenPopup}
            handleClose={() => setIsOpenPopup(false)}
            title="Zurückgehen"
            content="Sind Sie sicher, dass Sie zurückgehen wollen? Wenn Sie zurückgehen, gehen alle Informationen der Angehörigen verloren."
            up="Ja, zurückgehen"
            upCallback={handleConfirmDialog}
            down="Nein, bleiben Sie hier"
            downCallback={() => setIsOpenPopup(false)}
          />
        </AppointmentLayout>
      )}
    </Formik>
  );
};

export default AddRelativesForm;
