import React, { useEffect, useState } from 'react';

import { useAppointment } from 'store/appointment';

import { HBButton, HBModalConfirm } from 'hbBaseClass';
import { AppointmentBtnConfirm } from 'components/screens';
import AppointmentLayout from './Layout';
import WorkbookItem from 'views/user/mobility/WorkbookItem';

import { APPOINTMENT_STEPS } from 'configs/constants';
import { MODE_OPTIONS } from './AdviceForWhom';

// Prop types
type PropTypes = {
  onClickRelativeItem: (relativeIndex: number | undefined) => void;
  setMode: (value: number) => void;
};
const MAX_RELATIVES_ITEMS = 2;

const ListAdviceRelatives = (props: PropTypes) => {
  const { appointmentData, setStep, updateAppointmentData } = useAppointment();
  // props
  const { onClickRelativeItem, setMode } = props;
  // states
  const [isOpenPopup, setIsOpenPopup] = useState(false);
  const [menuOptionIndex, setMenuOptionIndex] = useState<number | undefined>(undefined);

  const { adviceRelatives = [] } = appointmentData;

  useEffect(() => {
    document.addEventListener('click', handleCloseMenuOptions);

    return () => {
      document.removeEventListener('click', handleCloseMenuOptions);
    };
  }, []);

  /**
   * Handle close menu options
   *
   * @param e DOM Event
   */
  function handleCloseMenuOptions(e: any) {
    const eleRelativeItems = Array.prototype.slice.call(
      document.getElementsByClassName('AdviceRelativeItem-container'),
    );
    const isValidMenu = eleRelativeItems.some((eleRelativeItem) => {
      return eleRelativeItem.contains(e.target);
    });

    if (!isValidMenu) setMenuOptionIndex(undefined);
  }

  /**
   * Handle remove relative item
   */
  const handleDeleteRelativeItem = () => {
    setMenuOptionIndex(undefined);

    const newAdviceRelatives = adviceRelatives.filter((item: any, index: number) => index !== menuOptionIndex);
    updateAppointmentData({ adviceRelatives: newAdviceRelatives });
  };

  /**
   * Handle click button go back
   */
  const handleGoBack = () => {
    if (adviceRelatives?.length) {
      setIsOpenPopup(true);
      return;
    }

    setMode(MODE_OPTIONS.MAIN);
  };

  /**
   * Handle confirm dialog
   */
  const handleConfirmDialog = () => {
    updateAppointmentData({ adviceRelatives: [] });
    setMode(MODE_OPTIONS.MAIN);
  };

  return (
    <AppointmentLayout title="Teilnehmer" onBack={handleGoBack}>
      <div className="AdviceForWhom-area">
        <div className="ListAdviceRelatives-area">
          <div className={`hb-card-video AppointmentLayout-workbooks ${!adviceRelatives?.length && 'empty'}`}>
            {adviceRelatives.map((relativeInfo: any, index: number) => (
              <div className="AdviceRelativeItem-container" key={index}>
                <WorkbookItem
                  label={`${relativeInfo.first_name} ${relativeInfo.last_name}`}
                  icon="user-square"
                  callback={() => setMenuOptionIndex(index)}
                />
                <div className={`AdviceRelativeItem-options ${menuOptionIndex === index && 'open'}`}>
                  <ul className="AdviceRelativeItem-menu">
                    <li onClick={() => onClickRelativeItem(index)}>
                      <i className="hb-icon-edit-2" /> Bearbeiten
                    </li>
                    <li onClick={handleDeleteRelativeItem}>
                      <i className="hb-icon-trash" /> Löschen
                    </li>
                  </ul>
                </div>
              </div>
            ))}
            {adviceRelatives?.length < MAX_RELATIVES_ITEMS && (
              <HBButton className="AdviceForWhom-btn" onClick={() => onClickRelativeItem(undefined)}>
                + Weitere Angehörige hinzufügen
              </HBButton>
            )}
          </div>
          <AppointmentBtnConfirm
            disabled={!adviceRelatives?.length}
            type="button"
            title="Weiter"
            onClick={() => setStep(APPOINTMENT_STEPS.SURVEY)}
          />
        </div>
      </div>
      <HBModalConfirm
        show={isOpenPopup}
        handleClose={() => setIsOpenPopup(false)}
        title="Zurückgehen"
        content="Sind Sie sicher, dass Sie zurückgehen wollen? Wenn Sie zurückgehen, gehen alle Informationen der Angehörigen verloren."
        up="Ja, zurückgehen"
        upCallback={handleConfirmDialog}
        down="Nein, bleiben Sie hier"
        downCallback={() => setIsOpenPopup(false)}
      />
    </AppointmentLayout>
  );
};

export default ListAdviceRelatives;
