import React, { useCallback, useEffect, useState } from 'react';
import { useHistory } from 'react-router';

import Search from 'components/search/Search';
import WorkbookItem from 'views/user/mobility/WorkbookItem';
import AppointmentLayout from './Layout';

import { SUCCESS, useApi } from 'store/callApi';
import { useAppointment } from 'store/appointment';
import { useAuth } from 'store/auth';

import config from 'config';
import { APPOINTMENT_STEPS } from 'configs/constants';

/**
 * Handle sort topics
 *
 * @param topics Topics
 */
const sortTopics = (topics: any) => {
  // sort topics by priority
  topics.sort((a: any, b: any) => (a.priority > b.priority ? -1 : 1));

  return topics;
};

const Topics = () => {
  const history = useHistory();
  const { callApi } = useApi();
  const { updateProfile } = useAuth();
  const { topics, setStep, updateTopics, updateAppointmentData, updateQuestions } = useAppointment();

  // states
  const [searchName, setSearchName] = useState('');
  const [isLoadingTopics, setIsLoadingTopics] = useState(true);

  /**
   * Get all topics
   */
  const getAllTopics = useCallback(() => {
    setIsLoadingTopics(true);
    callApi(
      {
        method: 'get',
        api: config.rest.getAllTreatments(),
        loading: true,
      },
      (response: any) => {
        setIsLoadingTopics(false);
        const { status, data } = response;
        if (status !== SUCCESS) return;

        // get data
        const { group_treatments = [] } = data || {};
        const listTopics = group_treatments.reduce((acc: any, groupTreatment: any) => {
          const { list_diseases = [] } = groupTreatment;

          return [...acc, ...list_diseases];
        }, []);

        updateTopics(sortTopics(listTopics));
      },
    );
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useEffect(() => {
    // Get Auth's profile
    const getProfile = () => {
      callApi(
        {
          method: 'get',
          api: config.rest.getProfileUser(),
          loading: true,
        },
        (response: any) => {
          const { status, data } = response;
          if (status !== SUCCESS) return;

          // update new auth's info when book an appointment
          const { gender, mobile, no, place, postcode, street } = data || {};
          updateProfile({ gender, mobile, no, place, postcode, street });
        },
      );
    };

    if (!topics?.length) {
      getAllTopics();
      getProfile();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  /**
   * Get suggest diseasess
   *
   * @param searchName
   */
  const getSuggestDiseasess = (searchName: string) => {
    if (!searchName.trim()) {
      getAllTopics();
      return;
    }

    callApi(
      {
        method: 'get',
        api: config.rest.getSearchTreatments(searchName),
        loading: true,
      },
      (response) => {
        const { data, status } = response;
        if (status !== SUCCESS) return;

        const { list_diseases = [] } = data || {};
        updateTopics(sortTopics(list_diseases));
      },
    );
  };

  /**
   * Handle click topic item
   *
   * @param topicId Topic id
   */
  const handleClickTopicItem = (topicId: number) => {
    updateQuestions([]);
    // call api get survey's questions
    callApi(
      {
        method: 'get',
        api: config.rest.getSurveys(topicId),
        loading: true,
      },
      (response) => {
        const { data, status } = response;
        if (status !== SUCCESS) return;

        const { steps: questions = [] } = data || {};

        updateAppointmentData({ selectedTopic: topicId });
        updateQuestions(questions);
        setStep(APPOINTMENT_STEPS.EMERGENCY_AID);
      },
    );
  };

  /**
   * Handle go back
   */
  const handleGoBack = () => {
    history.goBack();
  };

  return (
    <AppointmentLayout onBack={handleGoBack} onClose={handleGoBack}>
      <div className="AppointmentTopics-area">
        <Search
          className="AppointmentTopics-inputSearch"
          searchName={searchName}
          setSearchName={setSearchName}
          placeholder="z.B. Plegebedarf ermitteln"
          isNew
          minLengthSearch={0}
          callbackSearch={getSuggestDiseasess}
        />
        {!!topics.length ? (
          <>
            <h3 className="AppointmentLayout-title">Wählen Sie Ihr Beratungsthema</h3>
            <div className="hb-card-video AppointmentLayout-workbooks">
              {topics.map((topic: any, index: number) => (
                <WorkbookItem
                  className={topic.priority && 'high'}
                  key={index}
                  label={topic.tile}
                  icon="phone"
                  callback={() => handleClickTopicItem(topic.id)}
                />
              ))}
            </div>
          </>
        ) : (
          <>{!isLoadingTopics && <p className="AppointmentTopics-noResults">No results found</p>}</>
        )}
      </div>
    </AppointmentLayout>
  );
};

export default Topics;
